.userItem {
  display: flex;
  align-items: center;
  height: 100px;
  width: 100%;
  padding: 10px;
}

.userControl {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 70px;
  margin-right: 15px;
}

.avatarBox {
  width: 40px;
  height: 40px;
  margin-bottom: 10px;
  border-radius: 100%;
  overflow: hidden;
}

.avatarBox img {
  width: 100%;
  height: 100%;
}

.userInfo {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 10px;
  border: solid 2px orange;
  border-radius: 7px;
}

.aboutUser {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: start;
}

.location {
  display: flex;
  height: 100%;
  align-items: center;
  text-align: start;
}

.followBtn {
  width: 70px;
  padding: 5px;
  border: solid 1px black;
  border-radius: 5px;
}

.followBtn.followed {
  background: rgba(0, 128, 0, 0.650);
}

.followBtn.unFollowed {
  background: rgba(255, 0, 0, 0.650);
}
