.paginationControls {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.pageBtn {
  width: 50px;
  height: 30px;
  margin: 0 3px;
  border-radius: 3px;
  border: 1px solid black;
  background-color: transparent;
}

.pageBtn.activePage {
  background-color: gold;
  font-weight: bold;
}

.pageBtn:hover {
  border: 1px solid green;
  color: green;
  background-color: white;
}

.togglePageBtn {
  position: relative;
  width: 20px;
  height: 20px;
  margin: 0 3px;
}

.togglePageBtn:disabled {
  border: 1px solid grey;
  color: grey;
}

.togglePageBtn:disabled:hover {
  border: 1px solid grey;
  color: grey;
  background-color: transparent;
}

.togglePageBtn:active {
  background-color: gold;
}

.togglePageBtn::before {
  position: absolute;
  inset: 0;
  top: -2px;
  font-size: 18px;
}

.toggleLeftOver::before {
  content: '«';
}

.toggleLeftStep::before {
  content: '‹';
}

.toggleRightOver::before {
  content: '»';
}

.toggleRightStep::before {
  content: '›';
}



